import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { StockInfo, DividendsEntity } from "./models";
import {
  AnimatedAxis,
  AnimatedGrid,
  AnimatedLineSeries,
  XYChart,
} from '@visx/xychart';

const URL = window.location.origin

function download_data(market: string, ticker: string): Promise<void | StockInfo> {

  return fetch(`${URL}/data/${market}/${ticker}.json`, {
    method: 'GET',
  })
    .then(response => response.json())
    .then(data => {

      return data as StockInfo
    })
    .catch((error) => {
      console.error('Error:', error);
    });
}

function get_dividends(market: string, ticker: string): Promise<void | DividendsEntity[] | null | undefined> {
  return download_data(market, ticker)
    .then(response => {
      if (response != null) {
        return response.dividends
      }
    })
    .catch(error => {
      console.log(error)
    })
}

const DividensChart: React.FunctionComponent = () => {
  const [data, setData] = useState<{ x: string, y: number }[]>([]);
  const params = useParams<{ id: string, market: string }>();

  useEffect(() => {
    get_dividends(params.market, params.id).then(response => {
      let resp = response as DividendsEntity[]
      var data = []
      // convert date
      for (let item of resp) {
        let d = new Date(item.Date)
        data.push({
          x: d.toLocaleDateString(),
          y: item.Dividends,
        })
      }
      setData(data)
    })
  })

  const accessors = {
    xAccessor: (d: { x: string, y: number }) => d.x,
    yAccessor: (d: { x: string, y: number }) => d.y,
  };
  return (
    <XYChart height={300} xScale={{ type: 'band' }} yScale={{ type: 'linear' }}>
      <AnimatedAxis orientation="bottom" />
      <AnimatedAxis
        orientation="left"
        tickFormat={d => `$${d}`} />

      <AnimatedGrid columns={false} numTicks={3} />
      <AnimatedLineSeries dataKey="Line 1" data={data} {...accessors} />
    </XYChart>
  );

  // TODO: Add tooltip
  /*
   <Tooltip
      snapTooltipToDatumX
      snapTooltipToDatumY
      showVerticalCrosshair
      showSeriesGlyphs
      renderTooltip={({ tooltipData, colorScale }) => (
        <div>
          <div style={{ color: colorScale?(tooltipData?.nearestDatum.key) }}>
            {tooltipData.nearestDatum.key}
          </div>
          {accessors.xAccessor(tooltipData.nearestDatum.datum)}
          {', '}
          {accessors.yAccessor(tooltipData.nearestDatum.datum)}
        </div>
      )}
    />

  */
}

export default DividensChart;